@charset "UTF-8";
.analitics {
  position: relative;
  padding: 36px 0;
  border-top: 2px solid #809C8D;
}
@media only screen and (max-width: 1300px) {
  .analitics {
    padding: 16px 0;
  }
}
.analitics__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}
@media only screen and (max-width: 1300px) {
  .analitics__header {
    margin-bottom: 16px;
  }
}
.analitics h2 {
  font-size: 36px;
  font-weight: 600;
  line-height: 39.6px;
}
@media only screen and (max-width: 1300px) {
  .analitics h2 {
    font-size: 26px;
    line-height: 39.6px;
  }
}
.analitics a {
  color: #3F3F3F;
}
@media (hover: hover) {
  .analitics a:hover .analitics__card h3 {
    color: #EE8021;
  }
  .analitics a:hover .analitics__img img:first-child {
    transform: scale(1.1);
  }
}
.analitics__card {
  height: 100%;
  padding: 12px;
  border-radius: 12px;
  background-color: #F0F3F2;
  margin: 0 10px;
}
@media only screen and (max-width: 1300px) {
  .analitics__card {
    margin: 0 6px;
  }
}
.analitics__card h3 {
  font-size: 15px;
  font-weight: 500;
  line-height: 24px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Количество строк */
  -webkit-box-orient: vertical;
  color: #3F3F3F;
  margin-bottom: 8px;
  transition: color 0.3s;
}
.analitics__img {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  height: 246px;
  width: 100%;
  margin-bottom: 8px;
}
.analitics__img img:first-child {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  transition: transform 0.3s;
  object-fit: cover;
}
.analitics__info {
  font-size: 14px;
  font-weight: 400;
  line-height: 22.4px;
  color: #6F6D70;
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.analitics__info i {
  font-style: normal;
}
.analitics__info span:nth-child(2) {
  display: flex;
  align-items: center;
  gap: 8px;
}
.analitics__controls {
  display: flex;
  gap: 15px;
}
.analitics__controls .prev,
.analitics__controls .next {
  width: 42px;
  height: 42px;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.3s;
}
@media (hover: hover) {
  .analitics__controls .prev:hover,
  .analitics__controls .next:hover {
    background-color: rgba(128, 156, 141, 0.3294117647);
  }
}
.analitics__controls .next {
  transform: rotate(180deg);
}
@media (max-width: 950px) {
  .analitics__controls {
    display: none;
  }
}
.analitics .slick-slide {
  height: 366px !important;
}
.analitics .slick-track {
  display: flex !important;
  align-items: center !important;
}