@charset "UTF-8";
.wishes {
  position: relative;
  padding: 36px 0;
  border-top: 2px solid #809C8D;
}
@media only screen and (max-width: 1300px) {
  .wishes {
    padding: 16px 0;
  }
}
.wishes__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}
@media only screen and (max-width: 1300px) {
  .wishes__header {
    margin-bottom: 16px;
  }
}
.wishes h2 {
  font-size: 36px;
  font-weight: 600;
  line-height: 39.6px;
}
@media only screen and (max-width: 1300px) {
  .wishes h2 {
    font-size: 26px;
    line-height: 39.6px;
  }
}
.wishes a {
  color: #3F3F3F;
}
@media (hover: hover) {
  .wishes a:hover .wishes__card h3 {
    color: #EE8021;
  }
  .wishes a:hover .wishes__img img:first-child {
    transform: scale(1.1);
  }
}
.wishes__card {
  height: 100%;
  padding: 12px;
  border-radius: 12px;
  background-color: #fff;
  margin: 0 15px;
}
@media only screen and (max-width: 1300px) {
  .wishes__card {
    margin: 0 8px;
  }
}
.wishes__card h3 {
  font-size: 14px;
  font-weight: 500;
  line-height: 19.6px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Количество строк */
  -webkit-box-orient: vertical;
  color: #3F3F3F;
  margin-bottom: 8px;
  transition: color 0.3s;
}
.wishes__img {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  height: 170px;
  width: 100%;
  margin-bottom: 8px;
}
.wishes__img img:first-child {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  transition: transform 0.3s;
  object-fit: cover;
}
.wishes__img img:nth-child(2) {
  position: absolute;
  top: calc(50% - 18px);
  left: calc(50% - 18px);
  height: 36px;
  width: 36px;
  transition: transform 0.3s;
}
.wishes__info {
  font-size: 14px;
  font-weight: 400;
  line-height: 22.4px;
  color: #6F6D70;
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.wishes__info i {
  font-style: normal;
}
.wishes__info span:nth-child(2) {
  display: flex;
  align-items: center;
  gap: 8px;
}
.wishes__controls {
  display: flex;
  gap: 15px;
}
.wishes__controls .prev,
.wishes__controls .next {
  width: 42px;
  height: 42px;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.3s;
}
@media (hover: hover) {
  .wishes__controls .prev:hover,
  .wishes__controls .next:hover {
    background-color: rgba(128, 156, 141, 0.3294117647);
  }
}
.wishes__controls .next {
  transform: rotate(180deg);
}
@media (max-width: 950px) {
  .wishes__controls {
    display: none;
  }
}
.wishes .slick-slider {
  padding: 50px 0;
  background-color: #809C8D;
}
.wishes .slick-slide {
  height: 300px !important;
}
.wishes .slick-track {
  display: flex !important;
  align-items: center !important;
}