.squads {
  padding: 36px 0;
  border-top: 2px solid #809C8D;
}
@media only screen and (max-width: 1300px) {
  .squads {
    padding: 16px 0;
  }
}
.squads h2 {
  font-size: 36px;
  font-weight: 600;
  line-height: 39.6px;
  margin-bottom: 30px;
}
@media only screen and (max-width: 1300px) {
  .squads h2 {
    font-size: 26px;
    line-height: 39.6px;
    margin-bottom: 20px;
  }
}
.squads__wrapper {
  display: flex;
  gap: 14px;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}
.squads__item {
  flex-shrink: 0;
  width: calc(12% - 7px);
  aspect-ratio: 1/1;
  padding: 15px;
  position: relative;
  border: 1px solid #D9D9D9;
  border-radius: 6px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 1200px) {
  .squads__item {
    width: calc(24% - 7px);
    padding: 10px;
  }
}
@media only screen and (max-width: 600px) {
  .squads__item {
    width: calc(31% - 7px);
  }
}