@charset "UTF-8";
.modal {
  display: none;
  border-radius: 16px;
  width: 610px;
  /* Скрываем стандартный крестик */
  /* Добавляем свою иконку */
}
.modal .partner__form-heading {
  margin-bottom: 32px;
  font-family: Optima;
  font-size: 24px;
  font-weight: 400;
  line-height: 31.2px;
  letter-spacing: -0.01em;
  color: #181C1D;
}
@media only screen and (max-width: 700px) {
  .modal .partner__form-heading {
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 18px;
  }
}
.modal .form {
  margin: 0 86px;
}
.modal .form__button {
  margin-top: 32px;
  height: 68px;
}
@media only screen and (max-width: 700px) {
  .modal .form__button {
    margin-top: 18px;
  }
}
@media only screen and (max-width: 1000px) {
  .modal .form {
    margin: 0;
  }
}
.modal .fancybox-button svg {
  display: none;
}
.modal .fancybox-button::before {
  content: "X"; /* Путь к вашей иконке */
  display: inline-block;
  width: 12px; /* Ширина иконки */
  height: 12px; /* Высота иконки */
  color: #EE8121;
}

body .fancybox-content {
  padding: 0 !important;
  background-color: #181C1D;
}