.header {
  position: fixed;
  z-index: 99;
  width: 100%;
  transition: background-color 0.3s, box-shadow 0.3s, height 0.3s, transform 0.3s;
  background-color: #24391D;
  color: #fff;
}
.header__title {
  font-size: 16px;
  font-weight: 600;
  line-height: 25.6px;
}
@media only screen and (max-width: 1400px) {
  .header__title {
    display: none;
  }
}
.header__search {
  margin-left: auto;
  width: 280px;
  border-radius: 6px;
}
.header__search form {
  position: relative;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}
.header__search input {
  padding: 10px 42px 10px 16px;
}
.header__search button {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 100%;
}
@media only screen and (max-width: 1700px) {
  .header__search {
    width: 180px;
  }
}
@media only screen and (max-width: 1000px) {
  .header__search {
    margin: 0 auto;
    border: 1px solid #393939;
    width: 70%;
  }
}
.header__social {
  display: flex;
  gap: 6px;
  margin: 0 30px;
}
.header__social a {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0077FF;
  transition: opacity 0.3s;
}
.header__social a:nth-child(2) {
  background-color: #FF7700;
}
.header__social a:nth-child(3) {
  background-color: #03A9F4;
}
.header__social a:nth-child(4) {
  background-color: #5FBC74;
}
@media (hover: hover) {
  .header__social a:hover {
    opacity: 0.9;
  }
}
@media only screen and (max-width: 1700px) {
  .header__social {
    margin: 0 20px;
  }
  .header__social a {
    width: 28px;
    height: 28px;
  }
}
@media only screen and (max-width: 1000px) {
  .header__social {
    margin: 0 auto;
  }
}
.header__label {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 44px;
  width: 44px;
  border-radius: 3px;
  background-color: #809C8D;
  font-size: 16px;
  font-weight: 600;
  line-height: 25.6px;
  flex-shrink: 0;
}
.header__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 12px 0;
  align-items: center;
  transition: padding 0.3s;
}
@media only screen and (max-width: 1200px) {
  .header__wrapper {
    padding: 6px 0;
  }
}
.header__nav a {
  white-space: nowrap;
  font-size: 16px;
  font-weight: 400;
  line-height: 25.6px;
  text-align: left;
  color: #fff;
  transition: color 0.3s;
}
@media only screen and (max-width: 1700px) {
  .header__nav a {
    font-size: 15px;
    line-height: 20px;
  }
}
@media only screen and (max-width: 1000px) {
  .header__nav a {
    font-size: 18px;
    line-height: 22px;
    color: #24391D;
  }
}
.header__nav ul:first-child {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  margin: 0 30px;
}
@media only screen and (max-width: 1700px) {
  .header__nav ul:first-child {
    gap: 14px;
    margin: 0 20px;
  }
}
@media only screen and (max-width: 1000px) {
  .header__nav ul:first-child {
    flex-direction: column;
    margin: 0;
  }
}
.header__nav ul:nth-child(2) {
  display: flex;
  gap: 8px;
  flex-direction: column;
  width: max-content;
  list-style: none;
  position: absolute;
  top: 55px;
  left: auto;
  padding: 10px;
  margin: 0;
  border-radius: 6px;
  background-color: #ffffff;
  border: 1px solid #393939;
  box-shadow: 0px 0px 15px -2px #000000;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s;
}
.header__nav ul:nth-child(2) a {
  color: #24391D;
}
@media only screen and (max-width: 1000px) {
  .header__nav ul:nth-child(2) {
    position: relative;
    justify-content: center;
    gap: 10px;
    opacity: 1;
    pointer-events: auto;
    background-color: transparent;
    top: auto;
    padding: 0;
    border: none;
    text-align: center;
    box-shadow: none;
  }
}
@media (hover: hover) {
  .header__nav ul:nth-child(2) a:hover {
    color: #EE8121;
  }
}
.header__nav-caption {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: opacity 0.3s, color 0.3s;
}
.header__nav-caption svg path {
  transition: stroke 0.3s;
}
.header__nav-caption svg {
  transition: transform 0.3s;
}
@media only screen and (max-width: 1000px) {
  .header__nav-caption {
    display: none;
  }
}
@media (hover: hover) {
  .header__nav a:hover, .header__nav h3:hover {
    color: #EE8121;
  }
  .header__nav .header__nav-caption:hover svg path {
    stroke: #EE8121;
  }
}
.header__burger {
  position: absolute;
  right: 0;
  top: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 3px;
  background-color: #EE8121;
  flex-shrink: 0;
  padding: 4px;
  z-index: 100;
  cursor: pointer;
  transition: background-color 0.3s;
  display: none;
}
.header__burger i {
  display: block;
  position: relative;
  height: 2px;
  width: 60%;
  border-radius: 2px;
  background: #fff;
  left: 0;
  top: 0;
  margin-bottom: 4px;
  transition: opacity 0.3s, transform 0.3s;
}
.header__burger i:last-child {
  margin-bottom: 0;
}
@media only screen and (max-width: 1000px) {
  .header__burger {
    display: flex;
  }
}
.header .menu {
  display: contents;
}
@media only screen and (max-width: 1000px) {
  .header .menu {
    display: block;
    overflow-y: scroll;
    padding-bottom: 50px;
    position: absolute;
    z-index: 99;
    top: 101px;
    left: -20px;
    background-color: rgba(255, 255, 255, 0.9254901961);
    backdrop-filter: blur(5px);
    height: 100vh;
    width: calc(100vw + 20px);
    pointer-events: none;
    transform: translateX(-100%);
    transition: transform 0.3s;
  }
  .header .menu::-webkit-scrollbar, .header .menu::-webkit-scrollbar-thumb {
    display: none;
  }
}
.header .menu-scroll {
  display: contents;
}
@media only screen and (max-width: 1000px) {
  .header .menu-scroll {
    display: flex;
    gap: 16px;
    flex-direction: column;
    height: auto;
    width: 100vw;
    padding: 24px 20px 24px 30px;
    padding-bottom: 100px;
  }
}

body.is-nav-open {
  overflow: hidden;
}
body.is-nav-open .header .menu {
  transform: none;
  pointer-events: auto;
}
body.is-nav-open .header .header__burger i {
  transform: rotate(45deg);
}
body.is-nav-open .header .header__burger i:first-child {
  opacity: 0;
}
body.is-nav-open .header .header__burger i:last-child {
  transform: rotate(-45deg);
  top: -6px;
}

body.is-sub-nav-open .header__nav ul:nth-child(2) {
  opacity: 1;
  pointer-events: auto;
}
body.is-sub-nav-open .header__nav-caption svg {
  transform: rotate(90deg);
}

body.is-scrolled .header {
  backdrop-filter: blur(10px);
}
@media only screen and (max-width: 1000px) {
  body.is-scrolled .header {
    backdrop-filter: none;
  }
}
body.is-scrolled .header__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 6px 0;
  align-items: center;
}