form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
form .form__wrapper {
  display: flex;
  align-items: center;
  gap: 14px;
}
@media (max-width: 800px) {
  form .form__wrapper {
    flex-direction: column;
  }
}
form .form__label {
  display: block;
  cursor: pointer;
}
form .form__button {
  width: 100%;
  height: 74px;
}
form input {
  width: 100%;
  border: none;
  border-bottom: 1px solid #fff;
  padding: 24px;
  border-radius: 8px;
  font-size: 20px;
  font-weight: 400;
  line-height: 25.5px;
  text-align: left;
  color: #18191F;
  background-color: #fff;
}
form .checkbox {
  font-size: 14px;
  font-weight: 400;
  line-height: 26.8px;
  color: rgba(255, 255, 255, 0.4196078431);
  letter-spacing: normal;
  display: flex;
  text-align: left;
  justify-content: center;
}
form .checkbox label input {
  position: absolute;
  z-index: -1;
  height: 1px !important;
  width: 1px !important;
  opacity: 0 !important;
}
form .checkbox label > span {
  position: relative;
  display: block;
  cursor: pointer;
}
form .checkbox label > span:before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.1098039216);
  border-radius: 3px;
  margin-right: 10px;
  vertical-align: middle;
}
form .checkbox label > span:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  background-image: url("../img/check.svg");
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0;
  transition: 0.3s;
}
form .checkbox label input:checked + span:after {
  opacity: 1;
}