.footer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3F3F3F;
  color: #fff;
  width: 100%;
}
.footer__main h2 {
  font-size: 16px;
  font-weight: 500;
  line-height: 25.6px;
}
.footer__main p {
  font-size: 14px;
  font-weight: 400;
  line-height: 19.6px;
}
.footer__row {
  display: flex;
  gap: 2rem;
  width: 100%;
  justify-content: space-between;
}
@media only screen and (max-width: 800px) {
  .footer__row {
    flex-direction: column;
  }
}
.footer__info {
  max-width: 708px;
}
@media only screen and (max-width: 1200px) {
  .footer__info {
    width: 50%;
  }
}
@media only screen and (max-width: 800px) {
  .footer__info {
    width: 100%;
  }
}
.footer__links {
  display: flex;
  flex-direction: column;
  gap: 18px;
}
@media only screen and (max-width: 1200px) {
  .footer__links {
    gap: 12px;
  }
}
.footer a {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 25.6px;
  transition: color 0.3s;
}
@media only screen and (max-width: 1200px) {
  .footer a {
    font-size: 14px;
    line-height: 20px;
  }
}
@media (hover: hover) {
  .footer a:hover {
    color: rgba(255, 255, 255, 0.7333333333);
  }
}
.footer__up {
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #EE8121;
  transition: background-color 0.3s;
  padding-top: 4px;
}
@media (hover: hover) {
  .footer__up:hover {
    background-color: #db7114;
  }
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.footer__main {
  padding: 40px 0;
  border-bottom: 1px solid rgba(128, 156, 141, 0.3019607843);
}
@media only screen and (max-width: 1300px) {
  .footer__main {
    padding: 20px 0;
  }
}
.footer__bottom {
  padding: 20px 0;
}
@media only screen and (max-width: 1300px) {
  .footer__bottom {
    padding: 10px 0;
  }
}