.banner {
  margin-bottom: 25px;
}
.banner__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  gap: 32px;
  justify-content: space-between;
}
@media only screen and (max-width: 1350px) {
  .banner__wrapper {
    padding-bottom: 100px;
  }
}
@media only screen and (max-width: 1050px) {
  .banner__wrapper {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }
}
@media only screen and (max-width: 1000px) {
  .banner__wrapper {
    padding-bottom: 0;
  }
}
.banner__text {
  font-size: 16px;
  font-weight: 500;
  line-height: 25.6px;
  text-align: left;
}
@media only screen and (max-width: 1350px) {
  .banner__text {
    width: 80%;
    text-align: center;
  }
}
@media only screen and (max-width: 1000px) {
  .banner__text {
    width: 100%;
  }
}
.banner__row {
  display: flex;
  align-items: center;
  gap: 32px;
}
@media only screen and (max-width: 1350px) {
  .banner__row {
    flex-direction: column;
    gap: 20px;
  }
}
.banner__img {
  flex-shrink: 0;
  position: relative;
  background-color: #3c87a9;
  height: auto;
  width: 343px;
  overflow: hidden;
  border-radius: 6px;
}
.banner__img img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media only screen and (max-width: 1050px) {
  .banner__img {
    height: 210px;
    width: 100%;
    margin: 0 auto;
    background-color: transparent;
  }
}
.banner__logo {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: 34px;
}
.banner__logo img {
  width: 106px;
  height: 131px;
}
@media only screen and (max-width: 1400px) {
  .banner__logo img {
    width: 74px;
    height: 92px;
  }
}
@media only screen and (max-width: 800px) {
  .banner__logo img {
    width: 59px;
    height: 74px;
  }
}
.banner__logo span {
  display: inline-block;
  font-size: 42px;
  font-weight: 700;
  line-height: 46.2px;
  text-transform: uppercase;
}
@media only screen and (max-width: 1400px) {
  .banner__logo span {
    font-size: 32px;
    line-height: 36.2px;
  }
}
@media only screen and (max-width: 800px) {
  .banner__logo span {
    font-size: 22px;
    line-height: 26.2px;
  }
}
@media only screen and (max-width: 1400px) {
  .banner__logo {
    gap: 20px;
  }
}
.banner__nav {
  width: 100%;
  padding: 10px 20px;
  background-color: #EE8121;
  margin-top: 14px;
  border-radius: 6px;
}
@media only screen and (max-width: 1350px) {
  .banner__nav {
    position: absolute;
    bottom: 0;
  }
}
@media only screen and (max-width: 1000px) {
  .banner__nav {
    position: fixed;
    z-index: 98;
    top: 56px;
    border-radius: 0;
    left: 0;
    height: 44px;
    box-shadow: 0px 0px 15px -2px #000000;
    margin: 0;
    padding: 8px;
  }
}
.banner__nav a {
  font-size: 16px;
  font-weight: 600;
  line-height: 25.6px;
}
.banner__nav nav {
  position: relative;
}
.banner__nav nav a {
  white-space: nowrap;
  font-size: 16px;
  font-weight: 400;
  line-height: 25.6px;
  text-align: left;
  color: #fff;
  transition: color 0.3s;
}
@media only screen and (max-width: 1600px) {
  .banner__nav nav a {
    font-size: 14px;
    line-height: 20px;
  }
}
.banner__nav nav ul:first-child {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media only screen and (max-width: 1000px) {
  .banner__nav nav ul:first-child {
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    overflow-x: scroll;
  }
}
.banner__nav nav ul:first-child a {
  padding: 10px 10px;
  background-color: transparent;
  transition: background-color 0.3s, color 0.3s;
  border-radius: 3px;
}
@media (hover: hover) {
  .banner__nav nav ul:first-child a:hover {
    color: #fff;
    background-color: #3F3F3F;
  }
}
.banner__nav nav ul:nth-child(2) {
  display: flex;
  flex-direction: column;
  width: max-content;
  list-style: none;
  position: absolute;
  z-index: 1;
  top: 50px;
  left: auto;
  padding: 10px;
  margin: 0;
  border-radius: 6px;
  background-color: #ffffff;
  border: 1px solid #393939;
  box-shadow: 0px 0px 15px -2px #000000;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s;
}
.banner__nav nav ul:nth-child(2) a {
  display: inline-block;
  padding: 0 4px;
  color: #24391D;
  width: 100%;
  transition: background-color 0.3s, color 0.3s;
}
@media only screen and (max-width: 1000px) {
  .banner__nav nav ul:nth-child(2) a {
    color: #fff;
  }
}
@media only screen and (max-width: 1000px) {
  .banner__nav nav ul:nth-child(2) {
    position: relative;
    justify-content: center;
    gap: 10px;
    opacity: 1;
    pointer-events: auto;
    background-color: transparent;
    top: auto;
    padding: 0;
    border: none;
    text-align: center;
    flex-direction: row;
    box-shadow: none;
  }
}
@media (hover: hover) {
  .banner__nav nav ul:nth-child(2) a:hover {
    color: #fff;
    background-color: #EE8121;
  }
}
.banner__nav nav .banner__nav-caption {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: opacity 0.3s, color 0.3s;
}
.banner__nav nav .banner__nav-caption svg path {
  transition: stroke 0.3s;
}
.banner__nav nav .banner__nav-caption svg {
  transition: transform 0.3s;
}
@media only screen and (max-width: 1000px) {
  .banner__nav nav .banner__nav-caption {
    display: none;
  }
}
@media (hover: hover) {
  .banner__nav nav .banner__nav-caption:hover svg path {
    stroke: #EE8121;
  }
}
.banner__nav .menu {
  display: contents;
}
.banner__nav .menu-scroll {
  display: contents;
}

body.is-banner-nav-open .banner__nav nav ul:nth-child(2) {
  opacity: 1;
  pointer-events: auto;
}
body.is-banner-nav-open .banner__nav-caption svg {
  transform: rotate(90deg);
}